.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.round{
  border: solid 1px #fff;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  position: relative;
}

.needle{
 position: absolute;
 content: '';
 height: 85px;
 width: 3px;
 background-color: #fff;
 top: 50%;
 left: 50%;
 transform-origin: bottom center;
}

.needle.hour{
  transform: translate(-50%,-100%) rotate(0deg);
 }


 .needle.minute{
  transform: translate(-50%,-100%) rotate(0deg);
  height: 100px;
 }

 .needle.second{
  transform: translate(-50%,-100%) rotate(0deg);
  height: 120px;
  background-color: red;
  
 }



.centerpoint{
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}